import React, { Component } from 'react';
import { CardsInterface, CategoryInterface } from '../../api/api'; // eslint-disable-line import/extensions
import './AdminPage.css';

interface AdminPageProps {
  cards: CardsInterface;
  categories: CategoryInterface[];
}

interface AdminPageStatus {
  isEnabledUpdateMode: boolean;
}

class AdminPage extends Component<AdminPageProps, AdminPageStatus> {
  constructor(props: AdminPageProps) {
    super(props);
    this.state = {
      isEnabledUpdateMode: false,
    };
  }

  toggleUpdateMode = (): void => {
    const { isEnabledUpdateMode } = this.state;
    this.setState({ isEnabledUpdateMode: !isEnabledUpdateMode });
  }

  changeCategoryName = (): void => {
    this.toggleUpdateMode();
  }

  renderCategoryFront = (categoryCard: CategoryInterface): JSX.Element => {
    const { cards } = this.props;
    return (
      <div className="admin-page__card-front card-front">
        <button
          className="admin-page__delete-btn"
          type="button"
          aria-label="delete"
        />
        <h4 className="admin-page__card-title card-title">{categoryCard.name}</h4>
        <div className="admin-page__card-text">
          Words:
          <span className="word-counter">{cards[categoryCard.name].length}</span>
        </div>
        <div className="admin-page__btn-container">
          <button
            className="admin-page__btn update-btn"
            onClick={this.toggleUpdateMode}
            type="button"
          >
            update
          </button>
          <button
            className="admin-page__btn add-word-btn"
            type="button"
          >
            add word
          </button>
        </div>
      </div>
    );
  }

  renderCategoryBack = (card: CategoryInterface): JSX.Element => (
    <div className="admin-page__card-back card-back">
      <h4 className="admin-page__input-tip">Category Name</h4>
      <input
        type="text"
        className="admin-page__input"
        placeholder="category name"
        defaultValue={card.name}
        autoComplete="off"
      />
      <div className="admin-page__btn-container">
        <button
          className="admin-page__btn cancel-btn"
          onClick={this.toggleUpdateMode}
          type="button"
        >
          cancel
        </button>
        <button
          className="admin-page__btn submit-btn"
          onClick={this.changeCategoryName}
          type="button"
        >
          save
        </button>
      </div>
    </div>
  )

  renderCreateCardBtn = (): JSX.Element => (
    <li
      key="create"
      className="admin-page__card-container card-container"
    >
      <div className="admin-page__card card">
        <h4 className="admin-page__input-tip">CreateNewCategory</h4>
        <button
          className="admin-page__btn add-category-btn"
          type="button"
        >
          +
        </button>
      </div>
    </li>
  )

  renderCategoryCard = (card: CategoryInterface): JSX.Element => {
    const { isEnabledUpdateMode: isEnableUpdateMode } = this.state;
    return (
      <li
        key={card.name}
        className="admin-page__card-container card-container"
      >
        <div className="admin-page__card card">
          {isEnableUpdateMode ? this.renderCategoryBack(card) : this.renderCategoryFront(card)}
        </div>
      </li>
    );
  };

  render(): JSX.Element {
    const { categories } = this.props;
    return (
      <>
        <h2 className="page-title">
          Admin Page
        </h2>
        <ul className="admin-page__container cards-page-container">
          {categories.map((card: CategoryInterface): JSX.Element => this.renderCategoryCard(card))}
          {this.renderCreateCardBtn()}
        </ul>
      </>
    );
  }
}

export default AdminPage;
